<h1 id="服务器免密配置" tabindex="-1"><a class="header-anchor" href="#服务器免密配置" aria-hidden="true">#</a> 服务器免密配置</h1>
<p>创建账号
1.1 创建tidb账号
sudo useradd -m -d /home/tidb tidb
1.2 修改账号密码
sudo passwd tidb
1.3 授予账号sudo权限
sudo visudo
在 sudo 文件尾部加上 tidb ALL=(ALL) NOPASSWD: ALL，为 tidb 用户设置免密使用 sudo。</p>
<p>1.4 切换为tidb账户
su - tidb</p>
<p>1.5 为 tidb 用户创建 SSH 密钥
ssh-keygen -t rsa</p>
<p>2.配置互信
2.1 在每个节点上添加tidb账户，并且声称ssh证书
2.2 切换为tidb账户
2.3 当登录端口为非22端口时，需要添加全局ssh命令配置
执行命令vi ~/.ssh/config，在文本中添加</p>
<p>Host server
Hostname ip
Port 10086
也可以单独只加入Port一行配置</p>
<p>2.4 使用ssh-copy-id 将证书添加到各个节点
执行 ssh-copy-id [ip]</p>
<p>当提示bad owner or permission，需要更新config文件权限</p>
<p>chown -R tidb:tidb ~/.ssh/config</p>
<p>如果还是提示再，执行sudo chmod 600 ~/.ssh/config</p>
<p>再次执行 ssh-copy-id [ip]</p>
<p>然后使用ssh [ip]验证，是否可以免密登录</p>
<p>2.5 在各个节点上重复上述操作</p>
